import React from 'react';

const Events = require('../../lib/Events.js');
const classNames = require('classnames');
const TransformButtons = [
  { value: 'translate', icon: 'fa-arrows-alt' },
  { value: 'rotate', icon: 'fa-repeat' },
  { value: 'scale', icon: 'fa-expand' }
];

// eslint-disable-next-line react/display-name
export default () => {
  // constructor (props) {
  //   super(props);
  //   this.state = {
  //     selectedTransform: 'translate',
  //     localSpace: false
  //   };
  // }

  const [ selectedTransform, setSelectedTransform ] = React.useState('translate');
  const [ localSpace, setLocalSpace ] = React.useState(false);

  React.useEffect(() => {
    Events.on('transformmodechange', mode => {
      setSelectedTransform(mode);
    });

    Events.on('transformspacechange', () => {
      Events.emit('transformspacechanged', localSpace ? 'world' : 'local');
      setLocalSpace(!localSpace);
    });
  }, []);
  // componentDidMount () {
  //   Events.on('transformmodechange', mode => {
  //     this.setState({ selectedTransform: mode });
  //   });

  //   Events.on('transformspacechange', () => {
  //     Events.emit(
  //       'transformspacechanged',
  //       this.state.localSpace ? 'world' : 'local'
  //     );
  //     this.setState({ localSpace: !this.state.localSpace });
  //   });
  // }

  const changeTransformMode = mode => {
    setSelectedTransform(mode);
    Events.emit('transformmodechange', mode);
  };

  const onLocalChange = e => {
    const local = e.target.checked;
    setLocalSpace(local);
    Events.emit('transformspacechanged', local ? 'local' : 'world');
  };

  const renderTransformButtons = () => {
    return TransformButtons.map(
      function (option, i) {
        var selected = option.value === this.state.selectedTransform;
        var classes = classNames({
          button: true,
          fa: true,
          [option.icon]: true,
          active: selected
        });

        return (
          <a
            title={option.value}
            key={i}
            onClick={this.changeTransformMode.bind(this, option.value)}
            className={classes}
          />
        );
      }.bind(this)
    );
  };

  return <div id="transformToolbar" className="toolbarButtons">
    {renderTransformButtons()}
  </div>;
};
